@font-face {
	font-family: 'MontserratBlack';
	src: url('../../assets/fonts/Montserrat/MontserratBlack.woff2') format('woff2'), url('../../assets/fonts/Montserrat/MontserratBlack.ttf') format('truetype');
}

@font-face {
	font-family: 'MontserratMedium';
	src: url('../../assets/fonts/Montserrat/MontserratMedium.woff2') format('woff2'), url('../../assets/fonts/Montserrat/MontserratMedium.ttf') format('truetype');
}

@font-face {
	font-family: 'MontserratRegular';
	src: url('../../assets/fonts/Montserrat/MontserratRegular.woff2') format('woff2'), url('../../assets/fonts/Montserrat/MontserratRegular.ttf') format('truetype');
}

:root {
    --main-color: #B36975;
    --main-color-light: #ba4b5d; 
    --white-color: #F9F9F9;
    --green-color: #3DC74B;
    --gray-color: #d6d6d6;
    --white-background: rgba(255, 255, 255, 0.2);
    --black-background: rgba(0, 0, 0, 0.4);
    --main-radius: 10px;
}