@import url('../root.module.css');

.main {
    display: flex;
    flex-direction: column-reverse;
}

.task {
    display: grid;
    grid-template-columns: 92% 8%;
    margin: 0 0 30px 0;
}

.input {
    color: red;
}

.task_body {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    background: var(--black-background);
    margin: 0 2% 0 0;
    padding: 30px;
    border-radius: var(--main-radius);
}


.task_body .text {
    font-family: MontserratMedium;
    font-size: 24px;
    color: var(--white-color);
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    margin: 0 20px 0 0;
}

.task_body .time {
    font-family: MontserratRegular;
    font-size: 24px;
    color: var(--gray-color);
}

.buttons_wrapper {
    display: flex;
    justify-content: space-between;
}

.buttons_wrapper button {
    position: relative;
    height: 100%;
    width: 100px;
    border: none;
    outline: none;
    border-radius: var(--main-radius);
    background: var(--black-background); 
    cursor: pointer;
}

.icon {
    position: relative;
    width: 40%;
    height: 40%;
    top: 0;
    right: 0;
    fill: var(--gray-color);
}

.buttons_wrapper button:hover .complete_icon {
    fill: var(--green-color);
    transition: 0.2s;
}

.buttons_wrapper button:hover .edit_icon {
    fill: var(--white-color);
    transition: 0.2s;
}


/* @media */

@media screen and (max-width: 1399px) {
    .task {
        grid-template-columns: 91% 9%;
    }

    .task_body {
        margin: 0 3% 0 0;
    }
}

@media screen and (max-width: 1199px) {
    .task {
        grid-template-columns: 90% 11%;
    }

    .task_body {
        padding: 26px;
    }

    .task_body .text {
        font-size: 20px;
    }

    .task_body .time  {
        font-size: 20px;
    }

    .buttons_wrapper button {
        width: 87px;
    }
}

@media screen and (max-width: 959px) {
    .task {
        grid-template-columns: 89% 11%;
    }

    .task_body {
        margin: 0 4% 0 0;
    }

    .buttons_wrapper button {
        width: 76px;
    }
}

@media screen and (max-width: 767px) {
    .task {
        grid-template-columns: 100%;
    }

    .task_body {
        margin: 0;
    }

    .task_body .time {
        top: 12px;
        right: 17px;
        font-size: 16px;
    }

    .buttons_wrapper button {
        display: none;
    }
}

@media screen and (max-width: 519px) {
    .task_body .time {
        font-size: 20px;
    }
}
