@import url('../../styles/root.module.css');

.main {
    display: flex;
    flex-direction: column;
    margin: 0 0 80px 0;
}

.name {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 0 20px 0;
}

.title {
    font-family: MontserratBlack;
    color: var(--white-color);
    font-size: 40px;
    margin: 0;
    padding: 0;
}

.title span {
    color: var(--main-color);
}

.technology {
    font-family: MontserratRegular;
    font-size: 22px;
    color: var(--white-color);
}

.input_wrapper {
    display: grid;
    grid-template-columns: 70% 30%;
}

.input_wrapper input {
    padding: 24px;
    margin: 0 30px 0 0;
    font-family: MontserratRegular;
    font-size: 24px;
    color: var(--white-color);
    background: var(--white-background);
    border: none;
    border-radius: var(--main-radius);
    outline: none;
}

.input_wrapper input::placeholder {
    font-family: MontserratRegular;
    font-size: 24px;
    color: var(--gray-color);
    top: 32%;
}

.input_wrapper input:focus {
    outline: 1px solid gray;
    background: var(--black-background);
}

.input_wrapper button {
    padding: 27px 83px;
    background: var(--main-color);
    font-family: MontserratBlack;
    font-size: 24px;
    color: var(--white-color);
    border: none;
    border-radius: var(--main-radius);
    outline: none;
    cursor: pointer;
}

.input_wrapper button:hover {
    background: var(--main-color-light);
    transition: 0.2s;
}

.input_wrapper button:not(:hover) {
    transition: 0.2s;
}

@media screen and (max-width: 1199px ) {
    .title {
        font-size: 36px;
    }

    .input_wrapper input {
        font-size: 20px;
    }

    .input_wrapper input::placeholder {
        font-size: 20px;
    }

    .input_wrapper button {
        font-size: 20px;
        padding: 20px;
    }
}

@media screen and (max-width: 959px) {
    .input_wrapper {
        grid-template-columns: 1fr;
    }

    .main {
        margin: 0 0 50px 0;
    }

    .input_wrapper input {
        margin: 20px 0 20px 0;
    }
}

@media screen and (max-width: 519px) {
    .title {
        font-size: 30px;
    }

    .name {
        margin: 0 5px 0 0;
    }

    .technology {
        font-size: 15px;
    }
}







