@import url('../root.module.css');

.main {
    display: flex;
    flex-direction: column;
}

.title {
    margin: 0 0 20px 0;
    padding: 0;
    font-family: MontserratMedium;
    font-size: 36px;
    color: var(--white-color);
}

.list {
    display: flex;
    flex-direction: column;
}

@media screen and (max-width: 1200px) {
    .title {
        font-size: 30px;
    }
}

@media screen and (max-width: 519px) {
    .title {
        font-size: 20px;
    }
}



